<style type="text/css">
.order-item {
  margin: 10px;
  padding: 8px;
  border:1px solid #ddd;
  border-radius: 4px;
  position: relative;
}
.order-item-detail {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 10px;
}
.order-item-id {
  font-size: 13px;
  position: relative;
  margin-bottom: 6px;
}
.order-status {
  position: absolute;
  right: 8px;
}
.order-item-book-img {
  margin-right: 20px;
}
.order-price-all {
  text-align: right;
  padding-bottom: 8px;
}
.order-button-1 {
  position: absolute;
  bottom: 8px;
  right: 120px;
}
.order-button-2 {
  margin-left: 74%;
}
.order-price {
  color: red;
}
.comfirmDelivery {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.cancelDelivery {
  position: absolute;
  bottom: 10px;
  right: 80px;
}
.deliveryData {
  padding-bottom: 8px;
}
</style>
<template>
  <div>
    <div class="order-item" v-if="order.book">
      <div class="order-item-id">
        <span>:{{order._id}}</span>
        <span class="order-status">{{orderStatus[order.status]}}</span>
      </div>
      <div class="order-item-detail">
        <img class="order-item-book-img" :src="order.book.images.small">
        <div>
          <p>{{order.book.title}}   {{order.book.subtitle}}</p>
          <p v-if="$route.name==='myOrder'">:{{order.book.user.name}}</p>
          <p v-if="$route.name==='mySellBook'">:{{order.user.name}}</p>
          <p>:<span class="order-price">{{order.book.sharePrice}}</span></p>
          <p>:<span class="order-price"> {{order.book.deliveryPrice === 0? '' : '' + order.book.deliveryPrice + '' }}</span></p>
        </div>
      </div>
      <p class="order-price-all">:<span class="order-price">{{order.book.sharePrice + order.book.deliveryPrice}}</span></p>
      <!---->
      <!---->
      <div v-if="$route.name === 'myOrder'">
        <p class="deliveryData" v-if="order.status===2" style="font-size:12px;text-align:right;">{{order.logistics.company}}{{order.logistics.number}}</p>
        <router-link :to="{name: 'pay', params: {orderID: order._id}}"><my-button class="order-button-1" v-if="order.status===0" @click="" type="danger" size="small"></my-button></router-link>
        <my-button @click.native="cancelOrder" class="order-button-2" v-if="order.status===0" @click="" type="danger" size="small"></my-button>
        <my-button class="order-button-2" v-if="order.status===1 || order.status===2" @click="requestRefund" type="danger" size="small"></my-button>
        <my-button class="order-button-2" v-if="order.status===5" @click="cancelRefund" type="danger" size="small"></my-button>
        <my-button class="order-button-1" v-if="order.status===2" @click="comfirmReceived" type="danger" size="small"></my-button>
      </div>
      <div v-if="$route.name === 'mySellBook'">
        <my-button @click.native="" class="order-button-2" v-if="order.status===0" @click="" type="danger" size="small"></my-button>
        <my-button @click.native="cancelOrder" class="order-button-1" v-if="order.status===0 || order.status===1" @click="" type="danger" size="small"></my-button>
        <my-button @click.native="" class="order-button-2" v-if="order.status===1" @click.native="readyDelivery = !readyDelivery" type="danger" size="small"></my-button>
        <my-button @click.native="cancelRefund" class="order-button-1" v-if="order.status===5" type="danger" size="small"></my-button>
        <my-button @click.native="comfirmRefund" class="order-button-2" v-if="order.status===5" type="danger" size="small"></my-button>
        <p class="deliveryData" v-if="order.status===2" style="font-size:12px;text-align:right;">
          {{logistics.company || order.logistics.company}}{{logistics.number || order.logistics.number}}
          </p>
      </div>
      <popup 
        pop-transition='popup-fade'
        v-model="readyDelivery"
      >
        <div style="width:300px;height:160px;background:#fff;">
          <field label="" placeholder="" v-model="logistics.company"></field>
          <field label="" placeholder="" v-model="logistics.number"></field>

          <my-button @click.native="" class="cancelDelivery" @click.native="readyDelivery = !readyDelivery" type="danger" size="small"></my-button>
          <my-button @click.native="" class="comfirmDelivery" @click.native="comfirmDelivery" type="danger" size="small"></my-button>
        </div>
      </popup>
      
      
      
      
      
      
    </div>
  </div>
</template>
<script type="text/javascript">
import { Button, Popup, Field, MessageBox } from 'mint-ui';

export default {
  props: ['order'],
  components: { 'my-button': Button, Popup,Field },
  data() {
    return {
      orderStatus: ['', '', '', '', '', '', ''],
      readyDelivery: false,
      logistics: {
        
      }
    }

  },
  methods: {
    cancelOrder() {
      MessageBox.confirm('?').then(() => {
        this.order.status = 4;
        this.$http.post(IP+':2017/cancelOrder', {orderID: this.order._id});
      }).catch((val) => {
        console.log(val);
      });
      
    },
    comfirmDelivery() {
      this.readyDelivery = false;
      this.order.status = 2;
      this.$http.post(IP+':2017/comfirmDelivery', {orderID: this.order._id,logistics: this.logistics});
    },
    comfirmReceived() {
      MessageBox.confirm('?').then(() => {
        this.order.status = 3;
        this.$http.post(IP+':2017/comfirmReceived', {orderID: this.order._id});
      }).catch((val) => {
        console.log(val);
      });
    },
    requestRefund() {
      MessageBox.confirm('?').then(() => {
        this.order.status = 5;
        this.$http.post(IP+':2017/requestRefund', {orderID: this.order._id});
      }).catch((val) => {
        console.log(val);
      });
    },
    cancelRefund() {
      MessageBox.confirm('?').then(() => {
        this.$http.post(IP+':2017/cancelRefund', {orderID: this.order._id})
        .then(({data}) => {
          this.order.status = data.status;
        });
      }).catch((val) => {
        console.log(val);
      });
    },
    comfirmRefund() {
      MessageBox.confirm('?').then(() => {
        this.order.status = 6;
        this.$http.post(IP+':2017/comfirmRefund', {orderID: this.order._id});
      }).catch((val) => {
        console.log(val);
      });
    }
  }
}
</script>


