<style type="text/css">
.header-wrap {
  width: 100%;
  height: 40px;

}
.msg-unread {
  color: #fff;
  position: absolute;
  z-index: 100;
  right: 10px;
  height: 40px;
  vertical-align: middle;
}
.msg-unread > span:nth-child(1) {
  line-height: 40px;
}
</style>
<template>
<div class="header-wrap">
  <el-header fixed :title="this.$route.name === 'bookdetail'? book_title : headerTitles[$route.name]">
    <a onclick="javascript:history.go(-1)" slot="left" v-show="!~['home', 'index', 'classShare', 'bookShare', 'explore'].indexOf($route.name)">
      <mt-button icon="back"></mt-button>
    </a>
    <a slot="right">
      <span> </span><badge size="small" type="error">3</badge>
    </a>
  </el-header>
</div>
</template>

<script type="text/javascript">
import {Header ,Badge} from 'mint-ui';
export default {
  data() {
    return {
      headerTitles:{
        'home': '',
        'explore': '',
        'outbook': '',
        'outclass': '',
        'userInfo': '',
        'signin': '',
        'signup': '',
        'classShare': '',
        'bookShare': '',
        'address': '',
        'myShareClass': '',
        'myShareBook': '',
        'myCollectClass': '',
        'myCollectBook': '',
        'editaddress': '',
        'myOrder': '',
        'pay': '',
        'mySellBook': ''
      },
      book_title: '',
      hasUnread: true,
    }
  },
  created() {
    BUS.$on('show_book_title', (val) => {
      this.$nextTick(() => {
        this.book_title = val;
      });
    });
    
  },
  components: {
    elHeader: Header,
    Badge
  }
}
</script>


