<style type="text/css">
.IsbnSearch {
  width: 100%;
  height: 36px;
  border: 1px solid #26a2ff;
  box-sizing: border-box;
  margin: 20px 0;
  text-indent: 10px;
}
.book-info-front {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
}
.book-info-front>img {
  margin: 10px 2%;
  transform: scale(0.7);
}
.book-info-front>div {
  transform: scale(0.9);
}
.mint-cell-title {
  width: 80px !important;
}
.publish-book {
  margin: 20px 0;
}
</style>
<template>
  <div class="page-navbar">
    <navbar class="page-part" v-model="selected">
      <tab-item id="1"></tab-item>
      <tab-item id="2"></tab-item>
    </navbar>
    <tab-container v-model="selected">
      <tab-container-item id="1">
        <input class="IsbnSearch" type="text" name="name" placeholder="ISBN" v-model="isbn">
        <my-button @click="filling" type="primary" size="large"></my-button>
      </tab-container-item>
      <tab-container-item id="2">
        <div class="book-info-front">
          <img :src="bookInfo.image">
          <div>
            <mt-field placeholder="" v-model="bookInfo.title"></mt-field>
            <mt-field v-show="bookInfo.subtitle" placeholder="" v-model="bookInfo.subtitle"></mt-field>
            <mt-field placeholder="" v-model="bookInfo.author"></mt-field>
            <mt-field placeholder="" v-model="bookInfo.publisher"></mt-field>
          </div>
        </div>
        <div class="book-info-other">
          <mt-field label="ISBN" placeholder="ISBN" v-model="bookInfo.isbn13"></mt-field>
          <mt-field label="" placeholder="" v-model="bookInfo.quality"></mt-field>
          <mt-field label="" placeholder="" v-model="bookInfo.pages"></mt-field>
          <mt-field label="" placeholder="" v-model="bookInfo.price"></mt-field>
          <mt-field label="" placeholder="" v-model="bookInfo.sharePrice"></mt-field>
          <mt-field label="" placeholder="0" v-model="bookInfo.deliveryPrice"></mt-field>
          <mt-field label=""  type="textarea" rows="4" v-model="bookInfo.summary"></mt-field>
          <mt-field label="" type="textarea" rows="4" v-model="bookInfo.catalog"></mt-field>
          <mt-field label="" type="textarea" rows="4" v-model="bookInfo.shareWord"></mt-field>
        </div>
        <my-button class="publish-book" @click="publishBook" type="primary" size="large"></my-button>
      </tab-container-item>
    </tab-container>
  </div>
</template>

<script>
import { Navbar, TabItem, TabContainer, Cell, TabContainerItem, Button, Toast, Indicator} from 'mint-ui';
export default {
  data() {
    return {
      selected: '1',
      isbn: '',
      bookInfo: {
      },
      updateData: {

      }
    };
  },
  components: {Navbar, TabItem, TabContainer, Cell, TabContainerItem, 'my-button': Button},
  props: ['userData'],
  methods: {
    filling() {
      Indicator.open();
      if (this.isbn.trim()!=='') {
        this.$http.jsonp('https://api.douban.com/v2/book/isbn/' + this.isbn)
          .then(({data}) => {
            Indicator.close();
            this.bookInfo = data;
            this.isbn = '';
            this.selected = '2';
          })
          .catch((val) => {
            Indicator.close();
            Toast({
              message: 'isbn',
              duration: 1000
            });
          });

      }else {
        Toast({
          message: 'isbn',
          duration: 1000
        });
      }
    },
    publishBook() {
      ({
        title: this.updateData.title,
        subtitle: this.updateData.subtitle,
        author: this.updateData.author,
        image: this.updateData.image,
        images: this.updateData.images,
        price: this.updateData.price,
        sharePrice: this.updateData.sharePrice,
        publisher: this.updateData.publisher,
        isbn13: this.updateData.isbn,
        quality: this.updateData.quality,
        catalog: this.updateData.catalog,
        summary: this.updateData.summary,
        shareWord: this.updateData.shareWord,
        deliveryPrice: this.updateData.deliveryPrice
      } = this.bookInfo);
      if(!this.userData._id) {
        Toast({
          message: '',
          duration: 1500
        });
        setTimeout( () => {
          location.href= IP + ":8090/#/signin";
        }, 1500);
        return;
      };
      this.updateData.user = this.userData._id;
      let canfetch = true;
      for(let key in this.updateData) {
        if (this.updateData[key] === undefined) {
          canfetch = false;
          Toast({
            message: '',
            duration: 2000
          }); 
        }
      };
      if (canfetch) {
        this.$http.post(IP+':2017/publish', this.updateData).then( ({body}) => {
          if (body==='success') {
            Toast({
              message: '',
              duration: 2000
            }); 
            setTimeout( () => {
              location.href= IP + ":8090/#/index";
            }, 2000);
          }
        });
      }
    }
    
  }
};
</script>


